							// });
							// this.util.token = res.data.data.userinfo.token;
							uni.switchTab({
								url: "../../index/index/index"
							});
						} else {
							this.util.showWindow(res.data.msg);
						}
					} else {
						this.util.showWindow("请求错误");
					}
				});

			}
